@use "." as this;
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";

.inputFile {
    @include Border.set($radius: beta, $width: beta, $style: gamma, $color: gray-500);

    * {
        @include Font.set($align: justify);
    }

    div[class~="filepond--panel-root"] {
        @include Color.set($bg: "transparent");
    }
    
    > div:first-of-type {
        @include Font.set($size: gamma, $weight: normal, $family: "default");

        label {
            > div {
                @include Color.set($fg: "gray-light");
                @include Flex.set($align: center);
                svg {
                    @include Margin.set($right: alpha);
                }
            }
        }
    }
}

.inputFileError {
    @include Border.set($radius: beta, $width: beta, $style: gamma, $color: error-400);
    
    > div:first-of-type {
        label {
            > div {
                @include Color.set($fg: "error-400");
            }
        }
    }
}