@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Element/Form/InputCheckbox";

.fileUpload {
    @include Margin.set($top: delta, $bottom: delta);
    @include Flex.set($justify: flex-start, $wrap: wrap);
    width: Layout.get-width(iota);

    & > * {
        @include Margin.set($right: beta);
        width: Layout.get-width(iota);
    }

    .fileInputTooltip {
        @include Flex.set($justify: flex-start, $align: center);
        width: calc(Layout.get-width(iota) + 3.5%);

        & > section {
            @include Flex.set($grow: alpha);
            @include Display.set($display: block);
            @include Margin.set($top: beta);

            width: Layout.get-width(kappa);
        }

        .tooltip {
            @include Margin.set($left: alpha);
        }
    }

    #{InputCheckbox.$selector} {
        @include Margin.set($top: beta);
    }
}
